import React, { useState, FormEvent } from 'react';
import axios from 'axios';
import ReCAPTCHA from "react-google-recaptcha"

import "../../translations/i18n";

import './styles.css';
import { useTranslation } from 'react-i18next';

function Contact() {
    const { t } = useTranslation();
    
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [body, setBody] = useState('');
    const [captcha, setCaptchaVal] = useState('');
    const [inUse, setInUse] = useState(false);
    
    function onChange(value: any) {
        setCaptchaVal(value);
    }
    
    function sendMail(e: FormEvent) {
        if(inUse)
            return;

        setInUse(true);
        e.preventDefault();

        axios.post('https://murilopereira-mailer.herokuapp.com/mail', {
            name,
            email,
            body,
            captcha
        }).then(() => {
            alert(t('contact_message_success'));            
        }).catch((error) => {
            switch(error.response.status) {
                case 500:
                    alert(t('contact_message_err500'));
                    break;
                case 400:
                    alert(error.response.data.message);
                    break;
                default:
                    alert(t('contact_message_error'));
                    break;
            }
        });

        setName('');
        setEmail('');
        setBody('');
        setCaptchaVal('');
        setInUse(false);
    }

    return (
        <div className="contactContainer"> 
            <div className="contactHeader">
                <p>{t('contact_title')}</p>
            </div>
            <div className="contactForm">
                <form action="" id="sendMessage">
                    <label htmlFor="name">{t('contact_name')}</label>
                    <input 
                        id="name" 
                        placeholder={t('contact_label_name')} 
                        type="text"
                        value={name} 
                        onChange={(e) => { setName(e.target.value) }} 
                    />
                    <label htmlFor="mail">{t('contact_email')}</label>
                    <input 
                        id="mail" 
                        placeholder={t('contact_label_email')} 
                        type="mail"
                        value={email} 
                        onChange={(e) => { setEmail(e.target.value) }}    
                    />
                    <label htmlFor="text">{t('contact_idea')}</label>
                    <textarea 
                        maxLength={300} 
                        placeholder={t('contact_label_idea')}
                        id="text"
                        value={body} 
                        onChange={(e) => { setBody(e.target.value) }}
                    >

                    </textarea>
                    <div className="contactFooter">
                        <ReCAPTCHA theme="dark" sitekey="6Ldf_r0ZAAAAACZ9GbErSax88V6jiKyDYhNG_UsK" onChange={onChange} />
                        <button onClick={sendMail} type="submit">{t("contact_button")}</button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default Contact;