import React from 'react';
import { useTranslation } from 'react-i18next';
import "../../translations/i18n";
import './styles.css';

interface projectProps {
    src: string,
    title?: string,
    href?: string,
    tech?: string,
}

const Projects: React.FC<projectProps> = (props) => {
    const {t} = useTranslation();
    
    return (
        <div className="projectItem">            
                <a href={props.href} target="_blank" rel="noopener noreferrer" className="anchorProject">
                    <img alt={props.title} className="image" width="100%" src={props.src} />
                </a>
                <div className="overlay">
                    <div className="overlayWrapper">
                        <div className="text">{props.title}</div>
                        <div className="techs">{props.tech}</div>
                        <button onClick={()=> window.open(props.href, "_blank", "noopener noreferrer")}>{t('portifloio_more')}</button>
                    </div>                
                </div>
        </div>
    );
}

export default Projects;